#contact {
  min-height: calc(100vh - 4rem);
  padding: 2rem 10%;
  background: #0a0a0a; /* dark base to match theme */
  color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}


.contact-title {
  font-size: 2rem;
  font-weight: 800;
  margin-bottom: 1rem;
  background: linear-gradient(90deg, #ffffff, #d1d1d1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 4px 14px rgba(255, 255, 255, 0.08);
  letter-spacing: 2px;
}

.contact-subtitle {
  font-size: 1rem;
  color: #ccc;
  margin-bottom: 4rem;
}

.contact-container {
  display: flex;
  justify-content: center;
  gap: 3rem;
  flex-wrap: wrap;
  max-width: 1000px;
  margin: 0 auto;
}

.contact-info {
  flex: 1;
  min-width: 250px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  text-align: left;
}

.info-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1rem;
  color: #e5e5e5;
}

.icon {
  font-size: 1.3rem;
  color: #e5e5e5;;
}

.social-links {
  margin-top: 1rem;
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.social-links a {
  font-size: 1.5rem;
  color: #e5e5e5;
  background: rgba(255, 255, 255, 0.05);
  /* border: 1px solid rgba(255,255,255,0.08); */
  padding: 0.6rem;
  border-radius: 8px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.social-links a:hover {
   color: #fff;
   background: rgba(255, 255, 255, 0.12);
  box-shadow: 0 0 12px rgba(255,255,255,0.2);
  }

.contact-form {
  flex: 1;
  min-width: 350px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-form input,
.contact-form textarea {
  padding: 0.9rem 1.2rem;
  border: none;
  color: #e5e5e5;
  font-size: 1rem;
  width: 100%;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.contact-form input:focus,
.contact-form textarea:focus {
  outline: none;
}

.contact-form button {
  background: rgba(255, 255, 255, 0.08);
  color: #fff;
  border: 1px solid rgba(255,255,255,0.12);
  padding: 1rem;
  font-size: 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.contact-form button:hover {
  background: rgba(255,255,255,0.15);
  border-color: #666;
  box-shadow: 0 0 14px rgba(255,255,255,0.2);
}

@media (max-width: 992px) {
  .contact-container {
    gap: 2rem;
  }
}

/* Mobile */
@media (max-width: 768px) {
  #contact {
    justify-content: flex-start;
    padding-top: 6rem;
  }

  .contact-container {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .contact-info {
    text-align: center;
    align-items: center;
  }

  .info-item {
    justify-content: center;
  }

  .social-links {
    justify-content: center;
  }

  .contact-title {
    font-size: 2.2rem;
  }

  .contact-subtitle {
    font-size: 0.9rem;
  }
}

/* Extra small screens */
@media (max-width: 480px) {
  .contact-title {
    font-size: 1.6rem;
  }

  .contact-form input,
  .contact-form textarea {
    font-size: 0.9rem;
  }

  .contact-form button {
    font-size: 0.9rem;
    padding: 0.8rem;
  }
}